import React from "react";
import Slider from "../../../slider/Slider";
import { Consumer } from "../../roi.context";
import SliderWrapper from "./withSlider";
import { getInfo } from "./sliders.lib";

const sliderProps = {
  displayValues: ["0%", "25%", "50%", "75%", "100%"],
  displayInfo: [
    "Not recording calls: 0%",
    "Enabling reps to get recording consent from customers: 25%",
    "Reviewing recordings at 1x: 50%",
    "Calls used in a coaching program: 75%",
    "Coaching program analysis shows incremental improvements: 100%"
  ]
};

const OutreachVoice = props => (
  <Consumer>
    {({ outreachVoice }) => (
      <SliderWrapper
        label="Outreach voice implementation"
        info={getInfo(sliderProps, outreachVoice)}
      >
        <Slider
          bottomRange={0}
          topRange={5}
          value={outreachVoice}
          onUpdate={props.onUpdate}
          displayValues={sliderProps.displayValues}
          keyName="outreachVoice"
        />
      </SliderWrapper>
    )}
  </Consumer>
);

export default OutreachVoice;
