import React from "react";
import Slider from "../../../slider/Slider";
import { Consumer } from "../../roi.context";
import SliderWrapper from "./withSlider";
import { getInfo } from "./sliders.lib";

const sliderProps = {
  displayValues: ["0%", "25%", "50%", "75%", "100%"],
  displayInfo: [
    <div>Protect what? 0%</div>,
    <div>Implemented unsubscribe links on all emails: 25%</div>,
    <div>Implemented process to throttle suspended users: 50%</div>,
    <div>No user suspensions in 30-days: 75%</div>,
    <div>No user suspensions in 90-days: 100%</div>
  ]
};

const DomainReputation = props => (
  <Consumer>
    {({ protectReputation }) => (
      <SliderWrapper
        label="Protecting your domain reputation"
        info={getInfo(sliderProps, protectReputation)}
      >
        <Slider
          bottomRange={0}
          topRange={5}
          value={protectReputation}
          onUpdate={props.onUpdate}
          displayValues={sliderProps.displayValues}
          keyName="protectReputation"
        />
      </SliderWrapper>
    )}
  </Consumer>
);

export default DomainReputation;
