import React from "react";
import Slider from "../../../slider/Slider";
import { Consumer } from "../../roi.context";
import SliderWrapper from "./withSlider";
import { getInfo } from "./sliders.lib";

const sliderProps = {
  displayValues: ["0%", "25%", "50%", "75%", "100%"],
  displayInfo: [
    <div>
      Completely open access, reps have permission to do anything (create and
      publish sequences, see all records, configure new rulesets, change the
      Plug-in, etc.)
      <div>
        <span role="img" aria-label="bullet">
          &#9726;
        </span>
        <span>Appropriate for an early-stage, fast-growing</span>
        environment
      </div>
    </div>,
    <div>
      Restricted prospects to only being in one sequence at a time: 25%
      <div>
        <span role="img" aria-label="bullet">
          &#9726;
        </span>{" "}
        <h href="https://share.getcloudapp.com/nOuNrXdo" target="_blankx">
          Configurable under default ruleset
        </h>
      </div>
    </div>,
    <div>
      Reconfigured profiles allow reps to only see Accounts and Prospects that
      they own: 50%
      <div>
        <span role="img" aria-label="bullet">
          &#9726;
        </span>{" "}
        Restricted ability to assign custom rulesets
      </div>
    </div>,
    <div>
      Build triggers to match internal sales processes regarding ownership: 75%
    </div>,
    <div>No emails sent to any prospects not owned by Outreach user: 100%</div>
  ]
};

const PermissionsAccess = props => (
  <Consumer>
    {({ permissionsAccess }) => (
      <SliderWrapper
        label="Hierarchy/access permissions implementation"
        info={getInfo(sliderProps, permissionsAccess)}
      >
        <Slider
          bottomRange={0}
          topRange={5}
          value={permissionsAccess}
          onUpdate={props.onUpdate}
          displayValues={sliderProps.displayValues}
          keyName="permissionsAccess"
        />
      </SliderWrapper>
    )}
  </Consumer>
);

export default PermissionsAccess;
